import React from 'react';
import InfiniteCarousel from 'react-leaf-carousel';

import {
    Card, CardBody,
    CardHeader,
} from 'reactstrap';

function Carrucelinfinite(props) {
    const { data } = props
    console.log(data, 'data');
    
    let mes_nombre = ['Enero', 'Febrero', 'Marzo', 'Abril', 'Mayo', 'Junio', 'Julio', 'Agosto', 'Septiembre', 'Octubre', 'Noviembre', 'Diciembre',]

    return (
        <InfiniteCarousel
            breakpoints={[
                {
                    breakpoint: 600,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                    },
                },
                {
                    breakpoint: 768,
                    settings: {
                        slidesToShow: 3,
                        slidesToScroll: 3,
                    },
                },
            ]}
            dots={false}
            showSides={false}
            sidesOpacity={.5}
            sideSize={.1}
            slidesToScroll={1}
            slidesToShow={4}
            scrollOnDevice={true}
            cycleInterval={true}
            arrows={true}
            pagingSeparator={'/'}
            slidesSpacing={2}
            incrementalSides={true}
        >
            {
                data.map((item, index) => {
                    return (
                        <div className="col-sm" key={index}>
                            <Card>
                                <CardHeader className="bg-primary">
                                    <h4> {mes_nombre[item.mes - 1]} {item.anio} </h4>
                                </CardHeader>
                                <CardBody className="bg-info text-dark">
                                    Gastos: <h3> {`$ ${new Intl.NumberFormat().format(item.precio_total_mes)}`} </h3>
                                    <hr></hr>
                                </CardBody>
                            </Card>
                        </div>
                    )
                })
            }

        </InfiniteCarousel>

    );
}

export default Carrucelinfinite;
